.y-banner {
	@include semi;
	background-color: #fff200;
	border: 0.12rem solid #000;
	border-radius: 3px;
	padding: 2rem;
	font-size: 2.4rem;
	text-align: center;
	width: 90%;
	margin: 2rem auto;
	&--notification {
		width: 70%;
		padding: 1rem;
		font-size: 1.95rem;
		border-radius: 0.4rem;
		@include italic;
		box-shadow: 0.1rem 0.2rem 16px 0px rgba(0, 0, 0, 0.4); } }

.gradient-banner {
	padding: 0.7rem;
	max-width: 35.5rem;
	margin: 0 auto 2rem;
	background-image: linear-gradient(45deg, rgb(229,232,5) 0%, rgb(255,0,0) 100%);
	box-shadow: 0.1rem 0.1rem 2rem 0.1rem rgba(0, 0, 0, 0.5);
	border: 0.15rem solid #ffffff;
	animation: fadeIn 1.5s;
	&__title {
		font-size: 2.4rem;
		@include semi;
		color: #ffffff;
		text-align: center;
		margin-bottom: 0.8rem; }
	&__icon {
		text-align: center;
		font-size: 7rem;
		display: flex;
		justify-content: center;
		.icon {
			display: block; } }
	&--shop {
		background-image: linear-gradient( 45deg, rgb(0,255,252) 0%, rgb(0,0,0) 99%); } }

@keyframes fadeIn {
	from {
		opacity: 0; } }
