.btn {
	@include semi;
	font-size: 1.7rem;
	text-decoration: none;
	color: #fff;
	padding: 1.6rem;
	display: flex;
	align-items: center;
	justify-content: center;
	min-width: 19.6rem;
	border: 0.2rem solid #fff;
	border-radius: 3rem;
	background-image: linear-gradient( 61deg, rgb(255,136,0) 0%, rgb(255,106,0) 99%);
	box-shadow: 0px 0.1rem 2rem 0.2rem rgba(255, 106, 0, 0.4);
	&--close {
		color: black; }
	&--short {
		min-width: 15rem; }
	&--sm {
		min-width: 16rem;
		padding: 1.2rem;
		font-size: 1.6rem;
		border-width: 0.12rem; }
	&--lg {
		min-width: 28rem;
		font-size: 2.4rem;
		padding: 1.9rem; }
	&--dark-red {
		background-image: linear-gradient( 61deg, rgb(225,0,25) 0%, rgb(0,0,0) 100%);
		box-shadow: 0px 0.1rem 2rem 0.2rem rgba(225,0,25, 0.4); }
	&--green {
		background-image: linear-gradient( 61deg, rgb(84,199,0) 0%, rgb(0,176,21) 99%);
		box-shadow: 0px 0.1rem 2rem 0.2rem rgba(0, 176, 21, 0.4); }
	&--white {
		background: #ffffff;
		border-color: #ff8400;
		color: #ff8400; }
	&--green-grad {
		background-image: linear-gradient( 61deg, rgb(0,255,252) 0%, rgb(0,0,0) 99%);
		box-shadow: 0px 0.1rem 2rem 0.2rem rgba(0,255,252, 0.4); }
	&--red-grad {
		background-image: linear-gradient( 61deg, rgb(229,232,5) 0%, rgb(255,0,0) 99%);
		box-shadow: 0px 0.1rem 2rem 0.2rem rgba(255,0,0, 0.4); }
	&--blue {
		background-image: linear-gradient( 61deg, rgb(5,170,232) 0%, rgb(24,0,255) 99%);
		box-shadow: 0px 0.1rem 2rem 0.2rem rgba(24,0,255, 0.4); }
	&--violet {
		background-image: linear-gradient( 61deg, rgb(203,10,249) 0%, rgb(138,16,243) 72%, rgb(72,21,236) 99%);
		box-shadow: 0px 0.1rem 2rem 0.2rem rgba(72,21,236, 0.4); }
	&--rainbow {
		color: #000000;
		background-image: linear-gradient( 40deg, rgba(255,255,255,0.5) 10%,rgba(255,255,255,0.25) 15%, rgba(255,0,0,0.5) 20%, rgba(255,252,0,0.5) 28%, rgba(1,180,57,0.5) 45%, rgba(1,207,156,0.5) 50%, rgba(0,234,255,0.5) 55%, rgba(0,119,200,0.5) 60%, rgba(0,3,144,0.5) 65%, rgba(128,2,171,0.5) 70%, rgba(255,0,198,0.5) 72%, rgba(255,0,198,0.9) 75%, rgba(255,255,255,0.8) 80%, rgb(255,255,255) 100%);
		box-shadow: 0px 0.1rem 2rem 0.2rem rgba(0,0,0, 0.4); }
	&--yellow {
		color: black;
		background: #f6ff00;
		border-color: black;
		box-shadow: 0px 0.1rem 2rem 0.2rem rgba(246, 255, 0, 0.4); }
	&--square {
		border-radius: 0.4rem;
		border: none;
		@include regular;
		background: #ff6c00;
		box-shadow: 0px 1px 0.5rem 0px rgba(55, 45, 33, 0.2); } }

.simple-btn {
	background-color: transparent;
	border: 0.1rem solid #ff6c00;
	border-radius: 0.4rem;
	@include semi;
	color: #ff6c00;
	font-size: 1.6rem;
	padding: 0.7rem;
	text-decoration: none;
	display: inline-block;
	&--add {
		padding: 1.2rem;
		font-size: 2rem;
		border-color: #cccccc;
		border-width: 1px;
		display: block;
		width: 100%;
		text-align: center; } }

.theme-link {
	color: #ff6c00;
	@include semi;
	text-decoration: none; }

.social-btns {
	display: flex;
	justify-content: center;
	.social-btn:not(:last-child) {
		margin-right: 2rem; } }

.social-btn {
	padding: 1rem 3rem;
	border-radius: 1rem;
	svg {
		height: 3rem;
		width: 2rem; }
	&--google {
		box-shadow: 0 0.1rem 0.1rem rgba(0,0,0,0.2); }
	&--facebook {
		background-color: #3a5999; }
	&--twitter {
		background-color: #00acee; } }

.forget-link {
	margin: 2rem 0;
	display: block;
	text-align: center;
	@include semi;
	font-size: 2rem;
	text-decoration: none;
	color: #868686; }

.resend {
	margin: 2rem 0;
	display: block;
	text-align: center;
	font-size: 1.95rem;
	text-decoration: none;
	color: #000; }

.feedback-link {
	font-size: 1.4rem;
	display: inline-block;
	background-color: #002aff;
	text-decoration: none;
	color: #fff;
	@include semi;
	border: 0.15rem solid #FFFFFF;
	border-radius: 0.4rem;
	box-shadow: 0px 9px 16px 0px rgba(0, 0, 0, 0.79);
	padding: 0.1rem;
	&__stars {
		display: block;
		.icon {
			font-size: 1.1rem;
			fill: #ed8a19; }
		img {
			width: 1.2rem; } } }

.vertical-link {
	font-size: 1.8rem;
	@include semi;
	padding: 0 0.7rem 0.7rem 0.1rem;
	text-decoration: none;
	color: inherit;
	text-transform: uppercase;
	writing-mode: vertical-rl;
	text-orientation: upright;
	-webkit-text-orientation: upright;
	background-color: #00ffde;
	display: inline-block;
	border: 0.15rem solid #FFFFFF;
	border-radius: 0.4rem;
	line-height: 1;
	letter-spacing: -0.1rem;
	box-shadow: 0px 0.2rem 0.5rem 0px rgba(0, 0, 0, 0.79);
	&--green {
		background-color: #36ff00; }
	&--rose {
		background-color: #ff00d8; }
	&--orange {
		background-color: #ff6c00; } }

.allergen-link {
	font-size: 2rem;
	color: #ff0000;
	text-decoration: none;
	@include semi; }

.youtube-btn {
	display: block;
	background-color: transparent;
	.icon {
		font-size: 3.2rem;
		display: block;
		fill: #ff0000; } }
