.s-popup {
	z-index: 12;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: none;
	background-color: rgba(0, 0, 0, 0.75);
	&__inner {
		position: absolute;
		width: 90%;
		max-height: 50%;
		overflow: auto;
		left: 50%;
		top: 17rem;
		transform: translate(-50%, 0);
		border-radius: 0.5rem;
		border: 1px solid #000;
		padding: 1rem;
		background-color: #FFFFFF;
		box-shadow: 0px 5px 16px 0px rgba(0, 0, 0, 0.71);
		&--add-order {
			@include semi;
			text-align: center;
			font-size: 4rem;
			background-color: #fff200;
			-webkit-text-stroke: 0.05rem white;
			text-shadow: 0 0 0.2rem white; }
		&--choose {
			background-color: #d5d5d5; }
		&--serving {
			text-align: center;
			font-size: 2.7rem;
			color: #fc0000;
			line-height: 2; }
		&--error-message {
			font-size: 4rem;
			text-align: center;
			@include semi;
			padding: 3rem 2rem 1rem;
			line-height: 1.5; } }
	&__close {
		-webkit-text-stroke: initial;
		@include semi;
		text-decoration: none;
		position: absolute;
		top: 1rem;
		right: 1.5rem;
		background-color: transparent;
		font-size: 1.8rem; }
	&__ok-btn {
		font-size: inherit;
		background-color: transparent;
		color: #0090ec;
		line-height: 1; }
	&.is-active {
		display: block; } }

.modal {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	padding: 3.2rem 0.3rem;
	overflow: auto;
	&__content {
		position: relative;
		padding: 2rem; }
	&__close {
		color: red;
		@include semi;
		text-decoration: none;
		position: absolute;
		top: 1rem;
		right: 1.5rem;
		background-color: transparent;
		font-size: 1.8rem; }
	&.is-active {
		display: block; } }

.yellow-alert {
	background-color: #fffc00;
	width: 85%;
	border: 0.2rem solid red;
	box-shadow: 0.2rem 0.2rem 0.5rem 0 rgba(0,0,0,0.5);
	border-radius: 0.2rem;
	margin: 0 auto 2rem;
	position: relative;
	padding: 0.5rem;
	&__content {
		font-size: 4.05rem;
		@include semi;
		text-align: center; }
	&__close {
		line-height: 1;
		padding: 0.5rem;
		position: absolute;
		top: 0;
		right: 0.5rem;
		background-color: transparent;
		font-size: 1.5rem; } }

