.menu-slider {
	.slick-slide {
		padding: 1rem; }
	&__item {
		padding-top: 100%;
		border-radius: 0.5rem;
		background-color: #646464;
		position: relative;
		transition: 0.3s; }
	.slick-center {
		.menu-slider__item {
			transform: scale(1.2); } }
	&__content {
		@include coverdiv; } }

.loyalty-carousel {
	overflow: hidden;
	margin: 0 -2rem 1rem;
	padding: 0 12rem;
	.slick-track {
		display: flex;
		align-items: center; }
	.slick-list {
		overflow: visible; }
	.slick-slide {
		margin: 0 -2rem; }
	.slick-active {
		.lc-item {
			opacity: 1;
			&__content {
				opacity: 1;
				height: auto;
				display: block;
				transform: scaleY(1); }
			&__allergens {
				display: block; } } } }

.lc-item {
	padding-top: 1rem;
	opacity: 0.5;
	height: 100%;
	text-align: center;
	position: relative;
	&__content {
		opacity: 0;
		height: 0;
		transform: scaleY(0);
		transition: 0.3s;
		transform-origin: 0 0; }
	&__title {
		@include semi;
		font-size: 2.9rem; }
	&__desc {
		font-size: 1.95rem;
		p {
			margin: 0.5rem 0; } }
	&__price {
		color: #ff8700;
		@include raleway;
		font-size: 6.05rem; }
	&__image {
		width: 70%;
		margin: 0 auto;
		margin-bottom: 2rem;
		img {
			width: 100%; } }
	&__allergens {
		display: none;
		position: absolute;
		top: 0;
		right: 0;
		transform: translate(70%, 0); }
	&__loyalty {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		margin: 2rem -3rem;
		.bonus-earn {
			font-size: 2.2rem;
			align-items: center;
			white-space: nowrap;
			&__icon {
				font-size: 2.4rem; } }
		.bonus-pay {
			white-space: nowrap;
			font-size: 2.2rem; } } }

.loyalty-carousel-container {
	position: relative;
	&__btn {
		z-index: 1;
		position: absolute;
		left: 50%;
		top: 4rem;
		transform: translateX(-50%); } }

.loyalty-pts {
	font-size: 2.4rem;
	@include semi;
	text-align: center;
	margin-top: 0.5rem; }

.chart {
	height: 26rem;
	width: 26rem;
	margin: 2rem auto;
	position: relative;
	&__center {
		text-align: center;
		@include vcenter;
		color: #393939;
		font-size: 3.5rem; }
	&__title {
		z-index: -1;
		position: relative;
		margin-bottom: -3.5rem;
		font-size: 3.7rem;
		@include semi;
		color: black;
		-webkit-text-fill-color: black;
		-webkit-text-stroke: 0.04rem white;
		.icon {
			fill: #0078ff;
			font-size: 9rem; }
		span {
			position: absolute;
			top: 60%;
			left: 50%;
			transform: translate(-50%, -50%); } }
	&__value {
		color: black;
		-webkit-text-fill-color: black;
		-webkit-text-stroke: 0.07rem white;
		@include semi;
		font-size: 7.3rem; } }

.progress {
	transform: rotate(-90deg);
	height: 100%;
	width: 100%;
	&__value {
		transition: 1s;
		stroke-linecap: round; }
	&__meter, &__value {
		fill: none; }
	&__meter {
		stroke: #dadada; } }

.points {
	background-color: #FFFFFF;
	position: relative;
	padding: 1rem;
	border: 0.5rem solid black;
	box-shadow: 0.1rem 0.1rem 0.5rem rgba(0,0,0,0.5);
	&__title {
		@include semi;
		font-size: 2.25rem;
		text-transform: uppercase; }
	&__time {
		@include semi;
		font-size: 1.95rem; }
	&__items {
		margin: 1.6rem 0; }
	&__buttons {
		display: flex;
		justify-content: space-between;
		margin: 4rem 1rem;
		&--center {
			justify-content: center; } }
	&__total {
		padding: 0 2rem;
		font-size: 2.4rem;
		@include semi;
		display: flex;
		justify-content: flex-end;
		align-items: flex-end;
		border-top: 1px solid #dadada;
		border-bottom: 1px solid #dadada; }
	&__total-value {
		line-height: 1;
		margin-left: 6.5rem;
		font-size: 3.6rem;
		span {
			font-size: 1.45rem; } }
	&__total-title {
		margin-top: -0.3rem; }
	&--green-border {
		border-color: #1eff00; }
	&--rose-border {
		border-color: #ff01f0; } }

.point-item {
	display: flex;
	align-items: center;
	margin: 1rem 0;
	&__image {
		height: 9rem;
		min-height: 9rem;
		width: 9rem;
		border-radius: 50%;
		overflow: hidden;
		background-color: #464646;
		margin-right: 1rem;
		img {
			height: 100%;
			width: 100%;
			object-fit: cover; } }
	&__title {
		flex-grow: 1;
		@include italic;
		font-size: 1.8rem;
		color: #434343; }
	&__count {
		@include semi;
		font-size: 1.8rem;
		margin: 0 2rem; }
	&__delete {
		background-color: transparent;
		font-size: 3rem;
		fill: #434343;
		.icon {
			display: block; } } }

.points-text {
	margin: 5rem 0 3rem;
	@include semi;
	&__title {
		margin-bottom: 1rem;
		font-size: 2.4rem; }
	&__text {
		padding: 0 1.5rem;
		text-align: center;
		line-height: 1.5;
		font-size: 1.45rem; } }

.points-validation {
	margin-bottom: 1rem;
	padding: 1rem;
	background-color: #FFFFFF;
	animation: fromBottom 1s;
	box-shadow: 0.1rem 0.1rem 0.5rem rgba(0,0,0,0.5);
	&__title {
		@include semi;
		font-size: 1.95rem;
		text-align: center;
		margin-bottom: 1rem; }
	&__inputs {
		padding: 0 1rem;
		display: flex;
		justify-content: space-between;
		input {
			text-transform: uppercase;
			padding: 0.5rem;
			vertical-align: middle;
			border: 1px solid black;
			border-radius: 0.4rem;
			display: block;
			width: 19%;
			text-align: center;
			font-size: 2.9rem;
			@include semi;
			@include placeholder-color(#a8a8a8);
			&:not(:last-child) {
				margin-right: 1%; } } }
	&--green-bg {
		background-color: #1eff00; }
	&--rose-bg {
		background-color: #ff01f0;
		.points-validation__title {
			color: #FFFFFF; } } }

.loyalty-page {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 10rem 1rem 0;
	display: flex;
	flex-direction: column;
	.summary-table {
		flex-grow: 1; } }
