.start-page {
	min-height: 100vh;
	padding: 15.9rem 3rem 10rem;
	overflow: hidden;
	&__logo {
		width: 90%;
		margin: 0 auto 20.4rem; }
	&__btns {
		position: relative;
		z-index: 1;
		display: flex;
		justify-content: space-between; }
	&__title {
		opacity: 0;
		animation: fade-start-title 0.5s;
		animation-delay: 3s;
		animation-fill-mode: forwards;
		text-align: center;
		position: absolute;
		width: 100%;
		left: 0;
		font-size: 6rem;
		top: 7rem;
		@include semi; } }

@keyframes fade-start-title {
	to {
		opacity: 1; } }

.login-page {
	min-height: 100vh;
	padding: 6rem 3rem 5rem;
	overflow: hidden;
	position: relative;
	&__logo {
		width: 90%;
		margin: 0 auto 0; }
	&__title {
		@include regular;
		text-align: center;
		font-size: 3.2rem;
		margin-bottom: 3rem; }
	&__forget {
		text-decoration: none; }
	&__deco {
		position: absolute;
		top: 0;
		right: 0; } }

.allergen {
	margin: 2rem 0;
	display: flex;
	align-items: flex-start;
	&__title {
		@include semi;
		font-size: 2.2rem;
		color: #2c2c2c; }
	&__desc {
		font-size: 1.8rem;
		@include italic;
		padding-left: 1.5rem;
		color: #383838; }
	&__icon {
		min-width: 6rem;
		display: flex;
		justify-content: center;
		font-size: 4rem;
		margin-right: 2rem;
		.icon-bread {
			font-size: 3rem; } } }

.notification {
	margin: 4rem 0 10rem;
	text-align: center;
	&__icon {
		margin-bottom: 2rem;
		.icon {
			fill: #0000ff;
			font-size: 20rem; } }
	&__title {
		font-size: 3.22rem; } }

