.landing-decos {
	z-index: -1;
	position: absolute;
	bottom: 0;
	right: 0;
	width: 100%;
	animation: bubblesOut 4s;
	animation-fill-mode: forwards;
	animation-delay: 1.5s;
	span {
		animation: bubblesCome 2.5s;
		//animation-delay: 0.5s
		animation-fill-mode: forwards;
		position: absolute;
		&:nth-child(1) {
			bottom: 5.6rem;
			right: -6.3rem;
			height: 10.5rem;
			width: 10.5rem;
			border-radius: 50%;
			background-color: #b33df3; }
		&:nth-child(2) {
			bottom: 14.7rem;
