			border-radius: 50%;
			background-color: #e8ea62; }
		&:nth-child(6) {
			bottom: 33rem;
			right: 0;
			height: 2.7rem;
			width: 5rem;
			transform: rotate(26deg);
			background-color: #1200ff; }
		&:nth-child(7) {
			bottom: 21.2rem;
			right: 11.6rem;
			height: 3.3rem;
			width: 3.3rem;
			background-color: #00ff54; }
		&:nth-child(8) {
			bottom: 27.4rem;
			right: 10.4rem;
			height: 3.4rem;
			width: 3.4rem;
			border-radius: 50%;
			border: 0.4rem solid #000; } } }

@keyframes bubblesCome {
	from {
		bottom: 0;
		right: 0;
		opacity: 0;
		transform: translate(0) scale(0) rotate(0); }
	to {
		transform: translate(0) scale(1) rotate(30deg);
		opacity: 1; } }

@keyframes bubblesOut {
	to {
		transform: translate(-150%, 0) scale(1.5);
		opacity: 0; } }

