		border-radius: 50%;
		border: 0.5rem solid #000; }
	&__green-square {
		top: 17.4rem;
		left: -2.7rem;
		height: 4.2rem;
		width: 4.2rem;
		background-color: #00ff54; }
	&__red-square {
		transform: rotate(-13deg);
		top: 5.8rem;
