.offers {
	margin: 3rem 0;
	&__item {
		&:not(:last-child) {
			margin-bottom: 1.2rem;
			padding-bottom: 1.2rem;
			border-bottom: 1px solid #cccccc; } } }

.offer-item {
	$this: &;
	text-decoration: none;
	color: inherit;
	display: flex;
	border: 0.1rem solid #000;
	border-radius: 0.5rem;
	padding: 2rem 0.5rem 2rem 0.8rem;
	box-shadow: 0.1rem 0.2rem 0.2rem 0 rgba(0, 0, 0, 0.4);
	&__content {
		position: relative;
		flex-grow: 1; }
	&__button {
		height: 4rem;
		width: 4rem;
		min-width: 4rem;
		border-radius: 50%;
		border: 0.12rem solid #ff6c00;
		font-size: 2rem;
		margin-left: 2rem;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 1rem;
		.icon {
			fill: #ff6c00;
			position: absolute;
			top: 50%;
			left: 55%;
			transform: translate(-50%, -50%); } }
	&__value {
		margin-right: 1rem;
		@include semi;
		font-size: 4.02rem;
		height: 6.4rem;
		width: 6.4rem;
		min-width: 6.4rem;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		background-color: red;
		box-shadow: 0.2rem 0.2rem 1.8rem 0.2rem rgba(0, 0, 0, 0.75);
		color: #fff;
		text-shadow: 0.2rem 0.2rem 3px rgba(0, 0, 0, 0.4); }
	&__title {
		margin-bottom: 1.4rem;
		padding-right: 4rem;
		text-align: center;
		@include semi;
		font-size: 4.02rem;
		color: red;
		text-shadow: 0.2rem 0.2rem 3px rgba(0, 0, 0, 0.4);
		-webkit-text-fill-color: red;
		-webkit-text-stroke: 0.02rem white; }
	&__time {
		@include bold-italic;
		color: red;
		font-size: 2.4rem;
		margin-bottom: 1.3rem; }
	&__loyalty {
		.bonus-earn {
			font-size: 2.4rem;
			&__icon {
				margin-right: 1.7rem;
				.icon {
					font-size: 2.5rem; } } } }
	&--rose {
		#{$this}__value {
			background-color: #ff00d8; }
		#{$this}__title {
			color: #ff00d8;
			-webkit-text-fill-color: #ff00d8; }
		#{$this}__time {
			color: #ff00d8; } }
	&--green {
		#{$this}__value {
			background-color: #36ff00; }
		#{$this}__title {
			color: #36ff00;
			-webkit-text-fill-color: #36ff00; }
		#{$this}__time {
			color: #36ff00; } } }

.title-curtains {
	position: relative;
	&:before, &:after {
		z-index: 8;
		animation: scaleX 3s;
		//animation-fill-mode: forwards
		content: '';
		position: absolute;
		top: -5%;
		height: 115%;
		width: 50%;
		background-color: #fff;
		transform: scaleX(0); }
	&:before {
		left: 0;
		transform-origin: 0 100%; }
	&:after {
		right: 0;
		transform-origin: 100% 100%; } }

@keyframes scaleX {
	from {
		transform: scaleX(1); } }
