.orders {
	&__title {
		font-size: 2.9rem;
		margin-bottom: 1rem;
		text-align: center; } }

.order-item {
	display: flex;
	padding: 1.8rem 1rem 0.8rem 1rem;
	margin-bottom: 1rem;
	&__icon {
		margin-right: 3rem;
		font-size: 3.5rem;
		margin-top: 1rem;
		.icon {
			fill: #464646; } }
	&__info {
		flex-grow: 1; }
	&__title {
		margin-bottom: 0.7rem;
		font-size: 2.1rem; }
	&__status {
		font-size: 2rem;
		@include semi;
		color: #ff6c00; }
	&__price {
		line-height: 1;
		font-size: 4rem;
		@include raleway;
		color: #ff6c00; }
	&--border {
		border-bottom: 1px dashed #ababab; }
	&--order {
		.order-number__title {
			@include semi; }
		.order-item__price {
			font-size: 2.4rem; }
		.order-item__status {
			font-size: 2.4rem;
			@include semi; } } }

.orders-message {
	margin: 1.1rem 0 4rem;
	color: #3c00ff;
	&__title {
		font-size: 1.9rem;
		margin-bottom: 1rem; }
	&__text {
		letter-spacing: -0.05rem;
		@include semibold-italic; } }

.order-pos {
	display: flex;
	margin: 2.5rem 0;
	&__image {
		margin-right: 2rem;
		width: 10rem;
		min-width: 10rem;
		height: 10rem;
		border-radius: 0.4rem;
		background-color: #555555;
		box-shadow: 0.1rem 0.1rem 0.6rem 0 rgba(0, 0, 0, 0.2);
		overflow: hidden;
		img {
			height: 100%;
			width: 100%;
			object-fit: contain; } }
	&__info {
		flex-grow: 1;
		margin-top: 0.3rem; }
	&__title {
		font-size: 2.2rem;
		margin-bottom: 1rem;
		@include semi;
		color: #464646; }
	&__calcs {
		@include raleway;
		font-size: 2.1rem; }
	&__actions {
		display: flex;
		align-self: flex-start; }
	&__delete, &__edit {
		background-color: transparent;
		font-size: 3rem;
		.icon {
			fill: #464646; } }
	&__delete {
		margin-left: 1rem; } }

.border-message {
	margin: 2.8rem 0;
	color: #464646;
	padding: 0.5rem;
	border: 0.1rem solid #000000;
	border-radius: 0.6rem;
	&__title {
		font-size: 1.95rem;
		margin-bottom: 0.5rem; }
	&__text {
		line-height: 1.5;
		font-size: 1.6rem; } }

.bottom-message {
	font-size: 2.9rem;
	text-align: center;
	padding-top: 2rem;
	line-height: 1.5;
	border-top: 1px dashed #ababab; }

.orders-info {
	&__tab-links {
		display: flex;
		margin: 0 -2rem; }
	&__tab-link {
		transition: 0.3s;
		display: block;
		width: 50%;
		padding: 2rem;
		text-align: center;
		text-decoration: none;
		color: #2c2c2c;
		font-size: 2.15rem;
		background-color: #d4d4d4;
		box-shadow: 0 0 0.2rem 0 rgba(0, 0, 0, 0.5);
		&.is-active {
			background-color: transparent;
			color: #ff6c00;
			border-bottom: 0.15rem solid #ff6c00; } }
	&__content {
		display: none;
		&.is-active {
			display: block; } }
	&__button {
		margin: 3rem 0;
		display: flex;
		justify-content: flex-end; }
	&__order {
		border-bottom: 1px solid #cccccc; } }

.orders-info-list {
	font-size: 1.9rem;
	padding: 0.5rem 1rem 0.5rem 3rem;
	list-style: disc;
	line-height: 1.7;
	&__item {
		display: flex;
		span {
			padding: 0 0.5rem;
			&:nth-child(1) {
				width: 60%; }
			&:nth-child(2) {
				width: 20%; }
			&:nth-child(3) {
				@include raleway;
				text-align: right;
				width: 20%; } } } }
