.m-items {
	position: relative;
	z-index: 1;
	.message + & {
		margin-top: 2rem; }
	&:after {
		margin-top: 6rem;
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: calc(100% - 6rem);
		background-color: rgba(grey, 0.15);
		z-index: 1;
		pointer-events: none;
		opacity: 0;
		transition: 0.3s; }
	&__modal {
		position: relative;
		z-index: 5; }
	&.is-active {
		&:after {
			opacity: 1;
			pointer-events: auto; } } }

.m-item {
	display: flex;
	position: relative;
	align-items: center;
	margin: 1rem 0;
	padding-bottom: 0.8rem;
	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		height: 1px;
		width: 86%;
		background-color: #dadada; }
	&__content {
		display: block;
		text-decoration: none;
		color: inherit; }
	&__price {
		@include raleway;
		position: absolute;
		top: 0;
		right: 0;
		font-size: 2.4rem;
		background-image: url("../img/price-bage.png");
		background-size: 100% 100%;
		background-repeat: no-repeat;
		padding: 0.2rem 0.5rem 1rem 2rem;
		animation: fromRight 1s; }
	&__image {
		height: 10.8rem;
		width: 10.8rem;
		min-width: 10.8rem;
		border-radius: 50%;
		overflow: hidden;
		background-color: #ffffff;
		box-shadow: 2px 3px 12px 1px rgba(0, 0, 0, 0.75);
		img {
			height: 100%;
			width: 100%;
			object-fit: contain; } }
	&__title {
		letter-spacing: 0.06rem;
		font-size: 2.1rem;
		@include semi;
		margin-bottom: 1.6rem; }
	&__middle {
		padding: 0 1.4rem;
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__desc {
		line-height: 1.5;
		@include italic;
		font-size: 1.7rem;
		color: #434343; }
	&__btn {
		background-color: transparent;
		height: 4.4rem;
		width: 4.4rem;
		min-width: 4.4rem;
		border-radius: 50%;
		border: 0.1rem solid #ff6c00;
		position: relative;
		font-size: 2rem;
		margin-left: 2rem;
		.icon {
			fill: #ff6c00;
			position: absolute;
			top: 50%;
			left: 55%;
			transform: translate(-50%, -50%); } }
	&__bottom {
		margin-top: 2.1rem;
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 1.5rem;
		padding-right: 5.6rem; }
	&__pay-bonus {
		color: #0078ff;
		@include semi; } }

.item-modal {
	padding: 3rem 2rem 2rem;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 11;
	background-color: rgba(0,0,0,0.8);
	display: none;
	overflow: scroll;
	&__title {
		font-size: 2.4rem;
		color: #fff200;
		@include semi;
		margin-bottom: 0.5rem; }
	&__desc {
		margin-bottom: 4.6rem;
		font-size: 1.95rem;
		@include italic;
		color: #FFFFFF; }
	&__image {
		img {
			width: 100%;
			max-height: 100%; } }
	&--item {
		padding: 2rem;
		opacity: 0.9;
		background: linear-gradient(to bottom, rgba(255, 121, 0, 0.65) 0%,#ffffff 44%,#ffffff 100%);
		.item-modal__title {
			color: black; }
		.item-modal__desc {
			color: black;
			margin-left: 2rem; } } }

.item-banner {
	$this: &;
	position: relative;
	&__slider {
		@include coverdiv;
		height: 83%;
		div {
			height: 100%; }
		.slick-dots {
			bottom: -3.9rem;
			li {
				&:not(:last-child) {
					margin-right: 0.9rem; }
				button:before {
					height: 1.1rem;
					width: 1.1rem;
					background: transparent;
					border: 1px solid black; }
				&.slick-active {
					button:before {
						background: black; } } } } }
	&__slide {
		position: relative;
		height: 100%; }
	&__container {
		border: 0.1rem solid #000;
		box-shadow: 3px 4px 16px 0px rgba(0, 0, 0, 0.79);
		position: relative;
		width: 100%;
		padding-top: 47.5%; }
	&__pole {
		z-index: 1;
		position: absolute;
		top: 2rem;
		left: 4.5rem;
		width: 0.5rem;
		height: 83%;
		background-color: #000;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: #000;
			border-radius: 50%;
			height: 1rem;
			width: 1rem; } }
	&__image {
		padding: 1rem;
		@include coverdiv;
		img {
			height: 100%;
			width: 100%;
			object-fit: contain; } }
	&__image-label {
		position: absolute;
		top: 44%;
		left: 50%;
		transform: translate(-50%, -50%) rotate(-38deg);
		background-color: #f6ff00;
		font-size: 2rem;
		@include semi; }
	&__feedback {
		z-index: 2;
		position: absolute;
		//top: 3rem
		animation: flagToTop 2s;
		animation-fill-mode: forwards;
		top: 70%;
		left: -2rem;
		.feedback-link {
			border-radius: 0 0.4rem 0.4rem 0;
			border-width: 0.15rem 0.15rem 0.15rem 0; } }
	&__offers {
		z-index: 1;
		position: absolute;
		top: 2rem;
		right: -2rem;
		animation: fromRight 1.5s;
		animation-fill-mode: forwards;
		.vertical-link {
			border-radius: 0.4rem 0 0 0.4rem;
			border-width: 0.15rem 0 0.15rem 0.15rem; } }
	&__right-links {
		display: flex;
		flex-direction: column;
		z-index: 1;
		position: absolute;
		top: -7rem;
		right: -2rem;
		animation: fromRight 1.5s;
		animation-fill-mode: forwards;
		.vertical-link {
			border-radius: 0.4rem 0 0 0.4rem;
			border-width: 0.15rem 0 0.15rem 0.15rem;
			margin-bottom: 1.5rem; } }
	&__price {
		position: absolute;
		bottom: 0;
		right: 0.2rem;
		font-size: 3.6rem;
		animation: fromRight 1s; }
	&--offer {
		#{$this}__container {
			padding-top: 58%;
			border: none;
			box-shadow: none; }
		#{$this}__image {
			padding: 0; } } }

.item-details {
	&__loyalty {
		margin: 1.2rem 0;
		display: flex;
		justify-content: space-between;
		font-size: 2rem;
		padding: 0 7.2rem 0 2.3rem;
		.bonus-earn__icon {
			font-size: 1.8rem;
			margin-right: 0.5rem;
			align-self: center; } }
	&__variant {
		font-size: 1.95rem;
		margin-top: -1.5rem;
		margin-bottom: 0.5rem; }
	&__title {
		@include semi;
		font-size: 2.4rem; }
	&__desc {
		@include italic;
		font-size: 1.95rem;
		margin-bottom: 1.7rem;
		p {
			margin: 0.5rem 0; } }
	&__offer-time {
		@include semi;
		margin: 0 -2rem;
		background-color: #ff6c00;
		text-align: center;
		color: #fff;
		font-size: 2.5rem;
		padding-bottom: 0.5rem;
		&--green {
			background-color: #36ff00;
			color: black; }
		&--rose {
			background-color: #ff00d8;
			color: black; } } }

.total {
	margin: 0 -2rem 2rem;
	&__items {
		padding-top: 2.6rem;
		display: flex;
		scroll-snap-type: x mandatory;
		overflow-x: scroll; }
	&__item {
		scroll-snap-align: start;
		min-width: 100%; } }

.total-item {
	@include semi;
	&__title {
		text-align: center;
		color: red;
		margin-bottom: 1.6rem;
		font-size: 1.8rem;
		animation: fromTop 3s; }
	&__content {
		padding: 1rem 2rem 1.3rem;
		background-color: #757575; }
	&__top {
		display: flex;
		align-items: flex-end; }
	&__icon {
		width: 5.4rem;
		img {
			display: block;
			width: 100%; } }
	&__swipe {
		width: 4.1rem;
		animation: swipe 2s infinite forwards, flash 1s infinite;
		img {
			display: block;
			width: 100%; } }
	&__info {
		font-size: 2rem;
		flex-grow: 1;
		background-color: #FFFFFF;
		border-radius: 0.3rem;
		border: 0.1rem solid #0078ff;
		padding: 1.2rem 0;
		margin: 0 0.5rem;
		text-align: center;
		&--blue {
			background-color: #0078ff;
			color: #FFFFFF; }
		&--yellow {
			background-color: #fff200; } }
	&__bottom {
		font-size: 2.3rem;
		display: flex;
		padding: 2.9rem 1.8rem 0.8rem 1.9rem;
		margin: 0.7rem 1.1rem 0 1rem;
		position: relative;
		background-image: url("../img/total-bg.png");
		background-repeat: no-repeat;
		background-size: 100%	100%; }
	&__food {
		flex-grow: 1;
		color: #ff6e16; }
	&__count {
		margin-right: 0.6rem; } }

.quantity {
	padding: 0 1rem;
	&__title {
		@include semibold-italic;
		margin-bottom: 2.5rem; }
	&__content {
		font-size: 2.4rem;
		align-items: center;
		@include semi;
		display: flex;
		border-top: 1px solid #cccccc;
		border-bottom: 1px solid #cccccc;
		padding: 0.7rem 0; }
	&__count {
		margin-left: 1rem;
		height: 4rem;
		width: 4rem;
		border-radius: 50%;
		background-color: #000;
		color: #fff;
		display: flex;
		justify-content: center;
		align-items: center; }
	&__buttons {
		flex-grow: 1;
		display: flex;
		justify-content: flex-end; }
	&__button {
		background-color: #d1d0d0;
		position: relative;
		height: 4rem;
		width: 4rem;
		border-radius: 50%;
		margin-left: 2rem;
		.icon {
			font-size: 0.5rem;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%); }
		&--plus {
			.icon {
				font-size: 2rem;
				fill: red; } } }
	&--no-border {
		padding: 0;
		margin: 1rem 0;
		.quantity__content {
			border: none;
			padding: 0; } } }

.loyalty-bank {
	display: flex;
	align-items: center;
	@include semi;
	font-size: 2.4rem;
	padding: 0 1rem;
	&__count {
		margin-right: 0.8rem; }
	&__icon {
		font-size: 2.6rem;
		margin-right: 1.6rem;
		.icon {
			fill: #0078ff; } }
	&__text {
		color: #0078ff; } }

.choose {
	&__icon {
		width: 5rem;
		margin: 0 auto 1rem;
		img {
			width: 100%; } }
	&__title {
		background-color: #fff200;
		border-radius: 0.1rem;
		margin: 0 1rem 1rem;
		@include semi;
		padding: 0 1rem;
		font-size: 1.6rem; }
	&__text {
		background-color: #fff;
		border-radius: 0.3rem;
		padding: 1rem;
		font-size: 1.95rem;
		line-height: 1.5; }
	&__buttons {
		display: flex;
		justify-content: space-between;
		margin: 2rem 4rem; } }

.item-details-offer {
	display: flex;
	justify-content: space-between;
	padding: 1rem 6rem 1rem 1.3rem;
	&__price {
		animation: fromRight 1s;
		.price-box {
			font-size: 3.3rem; } } }

.item-details-offer-title {
	font-size: 2.9rem;
	@include semi;
	color: #ff6c00;
	text-shadow: 0.2rem 0.2rem 3px rgba(0, 0, 0, 0.4);
	-webkit-text-fill-color: #ff6c00;
	-webkit-text-stroke: 0.02rem white;
	sup {
		vertical-align: top;
		color: black;
		text-shadow: none;
		-webkit-text-fill-color: black;
		-webkit-text-stroke: initial; }
	&--green {
		-webkit-text-fill-color: #36ff00;
		color: #36ff00; }
	&--rose {
		-webkit-text-fill-color: #ff00d8;
		color: #ff00d8; } }

.s-items {
	display: flex;
	flex-wrap: wrap;
	&__item {
		width: 50%;
		padding: 2rem; } }

.s-item {
	border-radius: 0.4rem;
	overflow: hidden;
	width: 100%;
	padding-top: 100%;
	position: relative;
	background-color: #7c7c7c;
	&__content {
		padding: 2rem;
		@include coverdiv;
		img {
			height: 100%;
			width: 100%;
			object-fit: contain; } } }

@keyframes fromRight {
	from {
		transform: translateX(100%); }
	to {
		transform: translateX(0); } }

@keyframes fromTop {
	from {
		opacity: 0;
		transform: translateY(-100%); }
	to {
		opacity: 1;
		transform: translateY(0); } }

@keyframes flagToTop {
	to {
		top: 3rem; } }

@keyframes swipe {
	from {
		transform: translateX(0.3rem); }
	50% {
		transform: translateX(-0.3rem); }
	to {
		transform: translateX(0.3rem); } }

@keyframes flash {
	from {
		opacity: 0; }
	50% {
		opacity: 1; }
	to {
		opacity: 0; } }
