.s-form {
	margin: 2rem 0 4.5rem;
	&__btn {
		margin-top: 5.2rem;
		display: flex;
		justify-content: center; } }

.s-input {
	padding: 0 1rem;
	width: 100%;
	font-size: 2rem;
	@include regular;
	@include placeholder-color(#000);
	border-bottom: 1px solid #cccccc; }

.form-item {
	& + & {
		margin-top: 3rem; } }

.verification {
	input {
		border-radius: 0;
		width: 14%;
		@include semi;
		border-bottom: 0.6rem solid #000;
		font-size: 4.82rem;
		text-align: center; }
	&__inputs {
		margin: 1.7rem 0 7.4rem;
		display: flex;
		justify-content: space-between; } }

.taste-form {
	&__input, &__textarea {
		border: none;
		@include regular;
		color: #353535;
		@include placeholder-color(#353535);
		width: 100%;
		background-color: #adfcff;
		border-radius: 0.4rem;
		font-size: 2.4rem;
		padding: 0.5rem 1rem;
		margin: 1rem 0;
		&--sm-text {
			font-size: 1.45rem; } }
	&__label {
		color: #494949;
		@include semi;
		font-size: 1.95rem; }
	&__rate {
		margin: 1rem 0;
		border-radius: 0.4rem;
		display: flex;
		flex-direction: column;
		align-items: center;
		background-color: #adfcff; }
	&__rank-item {
		padding: 0.5rem 2rem;
		font-size: 1.95rem;
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include semi;
		margin: 1rem 0; } }

.rank {
	margin: 2rem 0 1rem;
	display: inline-block;
	.icon {
		font-size: 4rem; }
	&__rating {
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-around;
		text-align: center;
		input {
			display: none; }
		label {
			&:not(:last-child) {
				margin-left: 2rem; }
			.icon {
				fill: rgba(237, 138, 25, 0.71); } }
		input:checked ~ label {
			.icon {
				fill: #ed8a19; } }
		label:hover, label:hover ~ label {
			.icon {
				fill: #ed8a19; } } }
	&--sm {
		margin: 0;
		.icon {
			font-size: 2.8rem; }
		.rank__rating {
			label {
				&:not(:last-child) {
					margin-left: 0.5rem; }
				.icon {
					fill: rgb(185, 185, 185); } } } } }

.cart-form {
	&__textarea {
		border: none;
		padding: 0.7rem;
		border-radius: 0.35rem;
		margin: 1.8rem 1.4rem;
		display: block;
		width: 85%;
		@include semi;
		font-size: 1.7rem;
		@include placeholder-color(#000000); } }

.s-checkbox {
	input {
		display: none;
		&:checked ~ .s-checkbox__label {
			background-color: #000000;
			&:after {
				background-color: #fff;
				left: auto;
				right: 0.2rem; } } }
	&__label {
		display: block;
		width: 6rem;
		height: 2.8rem;
		border-radius: 1.5rem;
		background-color: #fff;
		position: relative;
		border: 0.1rem solid #000;
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			width: 2.4rem;
			height: 2.4rem;
			left: 0.2rem;
			border-radius: 50%;
			background-color: #000; } } }

.check {
	input {
		display: none;
		&:checked ~ .check__label {
			.icon {
				display: block; } } }
	&__label {
		height: 3rem;
		width: 3rem;
		border: 0.12rem solid black;
		display: block;
		position: relative;
		.icon {
			fill: #28d600;
			position: absolute;
			bottom: 0;
			left: 0;
			font-size: 3rem;
			display: none; } } }

.shadow-textarea {
	width: 90%;
	display: block;
	margin: 0 auto;
	box-shadow: 0.1rem 0.2rem 0.2rem 0px rgba(0, 0, 0, 0.75);
	font-size: 1.95rem;
	@include placeholder-color(black);
	@include regular;
	padding: 0.5rem; }

.password {
	position: relative;
	max-width: 37.5rem;
	margin: 2.6rem auto;
	&__input {
		width: 100%;
		font-size: 2.2rem;
		@include placeholder-color(#8e8e8e);
		@include regular;
		padding: 2.1rem 6.5rem 2.1rem 7.6rem;
		border: 1px solid rgb(204, 204, 204);
		box-shadow: 0.2rem 0.2rem 0.5rem 0px rgba(0, 0, 0, 0.46);
		border-radius: 0.3rem; }
	&__icon {
		position: absolute;
		top: 50%;
		transform: translateY(-45%);
		left: 3.5rem;
		.icon {
			font-size: 2.5rem;
			fill: #ff6c00; } }
	&__show {
		position: absolute;
		top: 50%;
		transform: translateY(-45%);
		right: 1.8rem;
		.icon {
			font-size: 3.5rem;
			fill: #ff6c00; } } }

.support-form {
	margin: 1.2rem 0; }

.support-input {
	display: block;
	width: 100%;
	border-bottom: 1px solid #959595;
	@include regular;
	@include placeholder-color(#444444);
	font-size: 2rem;
	padding: 1rem;
	margin-bottom: 4.2rem; }

.support-file {
	margin-bottom: 4.2rem;
	border-bottom: 1px solid #959595;
	input {
		display: none; }
	&__label {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 1rem; }
	&__placeholder {
		font-size: 2rem;
		color: #444444; }
	&__icon {
		font-size: 2.5rem;
		.icon {
			fill: #888888; } } }

.cart-textarea {
	margin: 1rem 0;
	border: none;
	border-radius: 0.4rem;
	padding: 0.5rem;
	display: block;
	width: 100%;
	font-size: 1.7rem;
	background-color: #e5e5e5;
	@include semi;
	@include placeholder-color(black); }

.delivery-radio {
	margin: 3rem 0;
	input {
		display: none;
		&:checked ~ {
			.delivery-radio__label:before {
				background-color: #ff6c00; }
			.delivery-radio__address {
				border-color: #ff6c00;
				.delivery-radio__icon {
					background-color: #ff6c00; } } } }
	&__label {
		font-size: 2rem;
		@include semi;
		position: relative;
		display: block;
		padding-left: 3rem;
		&:before {
			display: block;
			position: absolute;
			content: '';
			height: 2rem;
			width: 2rem;
			border-radius: 50%;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			background-color: #eeeeee; } }
	&__address {
		display: flex;
		border-radius: 0.5rem;
		align-items: flex-start;
		border: 1px solid #eeeeee;
		padding: 1rem;
		font-size: 1.9rem;
		line-height: 1.5; }
	&__icon {
		margin-top: 0.5rem;
		height: 2rem;
		width: 2rem;
		min-width: 2rem;
		border-radius: 50%;
		background-color: #eeeeee;
		margin-right: 2rem; }
	&__text {
		flex-grow: 1; }
	&__delete {
		background-color: transparent;
		font-size: 3rem;
		.icon {
			fill: #959595; } } }

